import React, {useContext} from "react";
import {motion} from "framer-motion";
import emoji from "react-easy-emoji";
import "./Greeting.scss";
import landingPerson from "../../assets/lottie/landingPerson";
import DisplayLottie from "../../components/displayLottie/DisplayLottie";
import SocialMedia from "../../components/socialMedia/SocialMedia";
import Button from "../../components/button/Button";
import {illustration, greeting} from "../../portfolio";
import StyleContext from "../../contexts/StyleContext";

export default function Greeting() {
  const {isDark} = useContext(StyleContext);
  if (!greeting.displayGreeting) {
    return null;
  }
  return (
    <motion.div
      initial={{ opacity: 0, y: 40 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 1 }}
    >
      <div className="greet-main" id="greeting" style={{ marginBottom: "100px", marginTop: "100px"}}>
        <div className="greeting-main">
          <div className="greeting-text-div" style={{ textAlign: "justify"}}>
            <div>
              <h1
                className={isDark ? "dark-mode greeting-text" : "greeting-text"}
              >
                {" "}
                {greeting.title}{" "}
              </h1>
              <p
                className={
                  isDark
                    ? "dark-mode greeting-text-p"
                    : "greeting-text-p subTitle"
                }
              >
                {greeting.subTitle}
              </p>
              <div id="resume" className="empty-div"></div>
              <SocialMedia />
              <div className="button-greeting-div">
                {greeting.resumeLink && (
                  <a
                    href={require("./resume_v1.2.pdf")}
                    download="Resume.pdf"
                    className="download-link-button"
                  >
                    <Button text="Resume"  />
                  </a>
                )}
              </div>
            </div>
          </div>
          <div className="greeting-image-div">
            <img
              alt="Hakan profile"
              src={require("../../assets/images/PixelArt_PhotoHakan.png")}
              style={{
                      width: "100%",
                      maxWidth: "300px",
                      borderRadius: "10px",
                      marginTop: "0px",   // space from top
                      marginLeft: "250px"   // space from left
                    }}
            />
          </div>
        </div>
      </div>
    </motion.div>
  );
}
