import React, { useContext } from "react";
import { bigProjects } from "../../portfolio";
import { motion } from "framer-motion";
import StyleContext from "../../contexts/StyleContext";
import Slider from "react-slick";
import img1 from "../../assets/images/SkywardStepsAnimatedHeader.gif"
import "./StartupProjects.scss";
import Divex from '../Divex.tsx'

export default function StartupProject() {
  function openUrlInNewTab(url) {
    if (!url) {
      return;
    }
    var win = window.open(url, "_blank");
    win.focus();
  }

  const { isDark } = useContext(StyleContext);
  if (!bigProjects.display) {
    return null;
  }
  return (
    <motion.div
      initial={{ opacity: 0, y: 200 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 1 }}
      viewport={{ once: true }}
    >
      <div className="main" id="projects" style={{ marginBottom: "150px"}}>
        <div>
          <h1 className="skills-heading"
            style={{ fontWeight: "bold", fontSize: "35px", paddingBottom: '16px' }}>{bigProjects.title}</h1>
          <p
            className={
              isDark
                ? "dark-mode project-subtitle"
                : "subTitle project-subtitle"
            }
          >
            {bigProjects.subtitle}
          </p>

          <div className="projects-container">
            {bigProjects.projects.map((project, i) => {
              return (
                <>

                <div
                  key={i}
                  className="project-wrapper"
                  >
                  {/* Project Card */}

                  <div
                    className={
                      isDark
                        ? "dark-mode project-card project-card-dark"
                        : "project-card project-card-light"}>

                    {project.image && (

                      <div className="project-image" style={{ padding: "15px", display:"flex", flexDirection: "column" }}>
                        <h5
                          className={isDark ? "dark-mode card-title" : "card-title"}
                          style={{ paddingBottom:"35px", fontSize: "30px", fontWeight: "bold", marginBottom: "0px" }}
                        >
                          {project.projectName}
                        </h5>
                        <img
                          src={project.image}
                          alt={project.projectName}
                          className="card-imageproject" />
                      </div>
                    )}
                    <div className="project-detail">

                      <p
                        className={isDark ? "dark-mode card-subtitle" : "card-subtitle"}
                        style={{ fontSize: "16px", marginBottom: "25px" }}
                      >
                        {project.projectDesc}
                      </p>
                      {project.footerLink && (
                        <div className="project-card-footer">
                          {project.footerLink.map((link, idx) => (
                            <span
                              key={idx}
                              className={
                                isDark ? "dark-mode project-tag" : "project-tag"
                              }
                              onClick={() => openUrlInNewTab(link.url)}
                            >
                              {link.name}
                            </span>
                          ))}
                        </div>
                      )}
                    </div>
                  </div>

                  <div className={"project-video-main"}>
                    <div className={"project-video-inner"}>
                      <header style={{fontSize:"30px", fontWeight:"bold"}}>Gameplay Trailer</header>
                      <iframe width="600" height="450" src={project.videoUrl}
                              title="YouTube video player" frameBorder="0"
                              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                              referrerPolicy="strict-origin-when-cross-origin"
                              allowFullScreen
                      ></iframe>
                    </div>
                  </div>


                </div>
                  <h1 className="skills-heading"
                      style={{ fontWeight: "bold", fontSize: "30px", paddingBottom: '50px' }}>{"Project Features"}</h1>
                  <Divex features={project.features} />
                </>
              );
            })}
          </div>
        </div>
      </div>
    </motion.div>
  );
}
